.toast_container { width: 100%; position: absolute; bottom: 0; left: 0; z-index: 99999; padding: 40px; display: flex; }
.toast { background-color: #3A3A3A; color: #fff; padding: 10px; border-radius: 2px; text-align: center; margin: auto; font-weight: 600; }
.toast b { font-weight: 700; color:var(--primary-color);}
.toast_fadein { animation: toast_fadein .35s ease; }
.toast_fadeout { animation: android_toast_fadeout .35s ease; animation-fill-mode: forwards; }
@keyframes toast_fadein{ from { opacity: 0; transform: translateY(50px); } to { opacity: 0.95; } }
@keyframes android_toast_fadeout { to { opacity: 0; } }