/**/


* { box-sizing: border-box; padding: 0px; margin: 0; font-family: 'UI'; font-size: 11px; -webkit-tap-highlight-color: transparent; outline: none; /*box-shadow:inset 0 0 0 1px red!important;*/}
html,body { height: 100%; width: 100%; overflow: hidden; border: none; margin: 0; padding: 0;}
body { perspective: 800px;}

::selection {background: var(--primary-color);color: #fff;}

.drag-area { -webkit-app-region: drag; color: rgba(255, 255, 255, 1); height: 50px; position: absolute; width: 100%; top: 0; left: 0; z-index: 99;}
.title { padding: 0 60px 0 20px; height: 100%; line-height: 50px; text-transform: uppercase; font-weight: bold; position: absolute; width: 100%; overflow: hidden; text-overflow: ellipsis; white-space: nowrap;}

input:focus::-webkit-input-placeholder,textarea:focus::-webkit-input-placeholder {transform: translateX(20px);opacity: 0;-webkit-user-drag: none}
input::-webkit-input-placeholder,textarea::-webkit-input-placeholder {transition: .2s;color: rgba(0, 0, 0, .5);text-transform: uppercase;font-weight: 700;-webkit-user-drag: none}
input[type=text],input[type=search] { height: 40px; width: 100%; padding: 0 10px; border: none; background: #fff; transition: none; color: rgba(0, 0, 0, .8); font-size: 11px; box-shadow: 0 0 0 1px rgba(0, 0, 0, .05), inset 0 2px 0 0 rgba(0, 0, 0, .02); text-overflow: ellipsis; overflow: hidden; font-weight: 600;}
input[type="search"]::-webkit-search-cancel-button {-webkit-appearance: none;}

textarea { padding: 10px; border: none; background: #fff; transition: none; overflow-y: auto; overflow-x: hidden; line-height: 20px; box-shadow: 0 0 0 1px rgba(0, 0, 0, .05), inset 0 2px 0 0 rgba(0, 0, 0, .02);}
textarea.validate-error {box-shadow:0 0 0 1px rgba(0, 0, 0, .05), inset 0 2px 0 0 rgba(0, 0, 0, .02),0 0 0 3px rgba(255, 87, 34, 0.25);}
textarea:focus,input[type=text]:focus,input[type=search]:focus {box-shadow: 0 0 0 1px rgba(0, 0, 0, .05), 0 8px 10px -4px rgba(0, 0, 0, .1);}

label { height: 30px; display: block; float: left; clear: both; padding: 0 10px 0 0; vertical-align: middle; line-height: 30px; position: relative; margin: 0 0 5px 0; white-space: nowrap;}
label input[type=checkbox] { height: 16px; width: 16px; margin: 7px 10px 7px 0; float: left;}
label input[type=radio] { height: 16px; width: 16px; margin: 7px 10px 7px 0; float: left; border-radius: 16px;}

label span { z-index: 2; font-weight: 600;}
label span span { padding: 0 10px; opacity: .5;}
input[type=checkbox],input[type=radio] { -webkit-appearance: none; box-shadow: inset 0 0 0 2px rgba(0, 0, 0, .65); z-index: 2; position: relative; transition: .125s;}

input[type=checkbox]:checked { box-shadow: inset 0 0 0 2px rgba(0, 0, 0, .05); background:var(--primary-color);}
input[type=radio]::after { content: ""; height: 8px; width: 8px; position: absolute; top: 4px; left: 4px; border-radius: 14px; transform: scale(0); transition: .125s; background:var(--primary-color);}
input[type=radio]:checked::after { transform: scale(1);}
input[type=radio]:checked { box-shadow: inset 0 0 0 2px; color: var(--primary-color);}


label:active input[type=radio]::before, label:active input[type=checkbox]::before{ transform: scale(1) }
input[type=radio]::before,input[type=checkbox]::before {  content: ""; transition: .125s; opacity: 0; position: absolute; height: 50px; width: 50px; opacity: .15; transform: scale(0); left: -17px; top: -17px; z-index: 1; border-radius: 50px; background:var(--primary-color);}
input[type=checkbox]::before {animation:chbox_anim_uncheck .3s backwards}
input[type=radio]:checked::before,input[type=checkbox]:checked::before {animation:chbox_anim .3s backwards}





@keyframes chbox_anim { from{transform:scale(1);} to{transform:scale(1.2); opacity:0}}
@keyframes chbox_anim_uncheck { from{transform:scale(1);} to{transform:scale(1.2); opacity:0}}


