/**/

.actions { position: absolute; top: 0; right: 0px; height: 50px; -webkit-app-region: no-drag; z-index: 100;}
.actions.left { left: 0; right: initial;}
.actions .button { width: 50px; height: 100%; position: relative; float: right; transition: .125s;}
.actions .button:hover > .icon { opacity: 1;}
.actions .button:active { background: rgba(255, 255, 255, .05);}
.actions .button.open:active { background: none;}
.actions .button.open:active > .icon { transform: none;}
.actions .button:active { transform: translateY(1px);}
.actions .button > .icon { position: absolute; top: 0; left: 0; right: 0; bottom: 0; margin: auto;}

/* icons */

.icon, icon { height: 30px; width: 30px;min-width: 30px; display: block; background-image: url(../images/imgres.svg); background-repeat: no-repeat; background-position: 0 0; color: transparent;}
.icon.plus { background-position: -30px 0;}
.icon.play_list { background-position: -30px 0;}
.icon.menu { background-position: -180px 0;}
.icon.media { background-position: -300px 0;}
.icon.feedback { background-position: -330px 0;}
.icon.settings { background-position: -360px 0;}
.icon.add { background-position: -390px 0;}
.icon.close { background-position: -210px 0; -webkit-app-region: no-drag;}
.icon.speaker { background-position: -240px 0; z-index: 102; transition: .2s;}
.icon.speaker:after {position: absolute;height: 60px;width: 60px;content: "";top: -15px;left: -15px;}

.icon.close { background-position: -210px 0; -webkit-app-region: no-drag;}
.icon.close_dark {background-position:-210px -30px }
.icon.mini {background-position: -420px 0;}
.icon.remove {background-position: -450px -30px}

icon.gplus {background-position: 0px -90px }
icon.twitter {background-position: -30px -90px }
icon.instagram {background-position: -60px -90px}
icon.dot {background-position: -180px -30px }
icon.folder {background-position: -480px -60px}
icon.folder_dark {background-position: -450px -60px}
icon.cloud_dark {background-position: -450px -90px}
icon.search_dark {background-position: -570px -60px}

.icon.add.large { background-position: -780px 0;}


icon.large{height: 60px;width: 60px; background-size: 1200px 1200px}

.icon.info {background-position: -480px 0px;}
.icon.shuffle {background-position: -300px -90px;}
.icon.repeat {background-position: -300px -60px;opacity: .4;transition: .2s;}
.repeat-one .icon.repeat {background-position: -300px -30px;opacity: 1;}
.repeat-all .icon.repeat { background-position: -300px -60px; opacity: 1;}
.open_settings .icon.mini,.open_source .icon.mini { background-position: -420px -30px;}
.open_settings .icon.close,.open_source .icon.close { background-position: -210px -30px;}
#vol.open .icon.speaker { background-position: -240px -30px; z-index: 102;}

.icon.cd {background-position: -210px -60px;}

.icon a, icon a{display: block;width: 100%;height: 100%;}

/**/


/* buttons */

button{color: #fff; height: 40px; text-align: center; line-height: 40px; border-radius: 2px; text-transform: uppercase; display: block; box-shadow: inset 0 0 0 1px rgba(0, 0, 0, .1); float: left; padding: 0 20px; border: none; font-weight: 800; font-size: 11px; white-space: nowrap; text-shadow: 0px 1px 0px rgba(0, 0, 0, 0.15); cursor: default; background:var(--primary-color);}
button:hover { color: #fff; background-image: linear-gradient(to bottom, rgba(0,0,0, .03) 0%, rgba(0,0,0, .03) 100%); }
button[disabled=disabled],button:disabled,button[disabled=disabled]:hover,button:disabled:hover,button[disabled=disabled]:active,button:disabled:active { background: rgba(0, 0, 0, 0.02); color: rgba(0, 0, 0, 0.2); text-shadow: 0px 1px 0px #fff; pointer-events: none;}
button:active {box-shadow: inset 0 0 0 1px rgba(0, 0, 0, .1), 0 0 0 3px rgba(0,0,0,.05); background-image: linear-gradient(to bottom, rgba(0,0,0, .03) 0%, rgba(0,0,0, .03) 100%); padding-top: 1px;}
button.white{ background-color: transparent; color: rgba(0, 0, 0, .7); text-shadow: none;}
button .icon,button icon { display: inline-block; vertical-align: top; margin: 5px 10px 0 -10px;}
button.white.mini{ min-width: auto; padding: 5px; width: 40px; box-shadow: none }
button.white.mini .icon { margin:0}
button.wide{width: 100%}

/**/


/*context menu*/


.contextmenu {animation: context_anim .2s ease-in-out both; transform-origin:left top; box-shadow: 0 5px 30px -3px rgba(0, 0, 0, .2), 0 0 0 1px rgba(0, 0, 0, .05);background: #fff;position: absolute;transition-duration: .125s;transition-property: opacity, transform, visibility;transform: scale(1);transform-origin: left top;z-index: 9999;}
.contextmenu .contextmenu {display: none; bottom:0;left: 10px;color: #000}

.contextmenu ul {padding: 10px 0;}
.contextmenu li {list-style: none; height: 40px;line-height: 40px;padding: 0 20px;min-width: 160px;text-transform: uppercase;font-weight: bold; animation: context_item_anim .2s ease-in-out both; white-space: nowrap;text-overflow: ellipsis;}
.contextmenu li:hover .contextmenu {display: block;}
.contextmenu li:nth-last-child(3) {animation-duration: .3s}
.contextmenu li:nth-last-child(2) {animation-duration: .4s}
.contextmenu li:nth-last-child(1) {animation-duration: .5s}
.contextmenu li:hover{color: #fff;background:var(--secondary-color);}

.contextmenu .separator {height: 1px;line-height: 1px;margin: 10px 0;float: none;pointer-events: none;padding: 0; display: none}
.contextmenu .separator:not(:last-child){display: block}

@keyframes context_anim {from{ transform: scale(.8);opacity: 0 }to{}}
@keyframes context_item_anim {from{ transform: translateY(-10px);opacity: 0 }to{}}


/**/


/* preloader */

.content.loader .preloader { display: block }

.preloader {height: 100%;width: 100%;z-index: 99;position: absolute;top: 0;left: 0; display: none}
.preloader div {height: 100%;width: 100%;position: absolute;top: 0;left: 0;opacity: .6;background:var(--secondary-color);}
.preloader span {width: 40px;height: 40px;top: 0;left: 0;right: 0;bottom: 0;position: absolute;margin: auto;}
.preloader span svg {animation: rotate 2s linear infinite;width: 40px;height: 40px;}
.preloader span svg circle {-webkit-stroke-dasharray: 1, 150;stroke-dasharray: 150%;stroke-dashoffset: 0;stroke-linecap: round;-webkit-animation: dash 1.6s ease-in-out infinite;stroke: #fff;}

@keyframes rotate {100% {transform: rotate(360deg);}}
@-webkit-keyframes dash { 0% {stroke-dasharray: 1, 150; stroke-dashoffset: 0; } 50% { stroke-dasharray: 90, 150; stroke-dashoffset: -35; } 100% { stroke-dasharray: 90, 150; stroke-dashoffset: -124; }}

/**/

.library_loader svg {animation: rotate 1.3s linear infinite;-webkit-animation: rotate 2s linear infinite;width: 20px;height: 20px;}
.library_loader svg circle {-webkit-stroke-dasharray: 1, 150;stroke-dasharray: 150%;stroke-dashoffset: 0;stroke-linecap: round;-webkit-animation: dash 1.6s ease-in-out infinite;stroke: #fff;}


.form_contianer { float: left; margin: 20px 0;}
.form_contianer textarea { width: 100%; height: 180px; resize: none; float: left; margin: 0 0 10px 0;}
.form_contianer input { float: left;}

.help_tip { padding: 20px 0; float: left;}
.help_tip h1 { margin: 10px 0;}
.help_tip h3 { margin:0 0 10px 0; font-size: 24px; font-weight: 300;}
.help_tip h4 { margin:0 0 10px 0; font-size: 16px; font-weight: 400; color: var(--primary-color);}
.help_tip p{ font-weight: 600; line-height: 16px; }



.command_panel { float: left; width: 100%; padding: 10px 0; margin: 10px 0;}
.command_panel button { float: right; margin: 0 0 0 10px;}

.tooltip { animation: tool_tip .125s 2s backwards ; pointer-events: none; white-space: nowrap; border-radius: 2px; padding: 0 10px; background:#616161; color: #fff; z-index: 999; font-weight: 600; line-height: 24px;}

@keyframes tool_tip { from {opacity: 0; }}


.hidden{ display: none;}



.material_ink{height:100%;width:100%;position:absolute;left:0;top:0;pointer-events:none; }
.material_ink circle{transform-origin:center center; display: none;}
.material_ink circle.clicked{animation:ink_anim .8s forwards;animation-timing-function:ease-out; display: block}

@keyframes ink_anim{from{r:0;}to{opacity:0;}}
